import React from 'react';
import './App.css';

function App() {
    
    function tellAJoke() {
        let jokes = ["It takes time to develop a web browser.\n\nChrome wasn't built in a day.", "My dog ate my computer science homework.\n\nIt took him a couple of bytes.", "What animal is the best at keeping your medical information confidential?\n\nA HIPAA-potamus"];
        let joke = jokes[Math.floor(Math.random() * jokes.length)];
        alert(joke);
    }

    return (
        <div className="App">
            <h1>Want to hear a lame joke?</h1>
            <button onClick={tellAJoke}>Hit me!</button>
            <br />
            <br />
            <a href="https://twitter.com/LameJokes69" style={{fontSize: "small"}}>Jokes courtesy of the <i>Lame Joke Of The Day</i> Twitter account.</a>
        </div>
    );
}

export default App;
